.colorChanger {
  padding: 20px;
}
input {
  width: 50px;
  height: 25px;
}
.colorChanger {
  padding: 50px;
  display: flex;
  flex-direction: row;
  align-items: center !important;
  align-content: center !important;
  width: 500px;
  margin: auto;
  height: auto;
}
.Customslider {
  margin: auto;
}
.rangeslider {
  width: 500px;
}
.colorType {
  border: 1.5px solid rgb(251, 244, 255);
  cursor: pointer;
  padding: 0 10px 0 10px;
}
.colorType {
  background-color: darksalmon;
  color: white;
}

.red {
  background-color: #ff0000 !important;
}
.blue {
  background-color: #0000ff !important;
}
.green {
  background-color: #00ff00 !important;
}
.colorCodeValue {
  color: #00ff00;
}
